import * as React from 'react';
import { MenuItem, InlineMenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { wrapLink } from '../actions';
import { LINK_INSERT_HOT_KEY_TIP } from '../utils/hotKeyTips';
import { MenuItemInnerProps } from '@ali/4ever-component';

function withLinkMenu(Component: React.FC<any>) {
  return function Menu(props: MenuItemInnerProps) {
    const { menuItem, controller, locale } = props;
    const { title, ...rest } = menuItem;

    const handleClick = () => controller.run('onAction', wrapLink());

    return (
      <Component extra={LINK_INSERT_HOT_KEY_TIP} {...rest} onClick={handleClick} role={PluginRoles.link}>
        {title || locale?.buttonTitle}
      </Component>
    );
  };
}

export const menu = withLinkMenu(MenuItem);

export const inlineMenu = withLinkMenu(InlineMenuItem);
